const checkPermission = () => {
    if (!('serviceWorker' in navigator)) {
        throw new Error("No support for service worker!")
    }

    if (!('Notification' in window)) {
        throw new Error("No support for notification API");
    }

    if (!('PushManager' in window)) {
        throw new Error("No support for Push API")
    }
}

const registerSW = async () => {
    const registration = await navigator.serviceWorker.register('/sw.js');
    return registration;
}

const requestNotificationPermission = async () => {
    const permission = await Notification.requestPermission();

    if (permission !== 'granted') {
        throw new Error("Notification permission not granted")
    }

}

const hasSubscription = async () => {
    // Получаем сервисный рабочий
    const swRegistration = await navigator.serviceWorker.getRegistrations();
  
    // Проверяем, есть ли зарегистрированный сервисный рабочий
    if (swRegistration && swRegistration.length > 0) {
      const sw = swRegistration[0];
  
      // Пытаемся получить доступ к контроллеру подписок
      const subscription = null;
      try {
        subscription = await sw.pushManager.getSubscription();
      } catch (err) {
        console.error("Ошибка при получении подписки:", err);
      }
  
      // Проверяем, есть ли активная подписка
      if (subscription) {
        // Подписка активна, возвращаем true
        return true;
      }
    }
  
    // Если нет активной подписки, возвращаем false
    return false;
  };
  

  

const main = async () => {
    checkPermission()
    await requestNotificationPermission()
    await registerSW()
}
export { main,hasSubscription };

