import { createSlice } from '@reduxjs/toolkit';

const postSlice = createSlice({
    name: 'post',
    initialState: {
      generation: '', // Добавлено поле для генерации
      booklet: 'Доступ', // Добавлено поле для булета
      accessType: 'Электронный',
      quantity: '',
      city: '',
      productIds: [] // Добавлено поле для хранения идентификаторов товаров
    },
    reducers: {
      setGeneration: (state, action) => {
        state.generation = action.payload;
      },
      setBuklet: (state, action) => {
        state.booklet = action.payload;
      },
      setAccessType: (state,action) => {
        state.accessType = action.payload;
      },
      setquantity: (state, action) => { 
        state.quantity = action.payload;
      },
      setCity: (state, action) => {
        state.city = action.payload;
      },
      setProductIds: (state, action) => { // Действие для обновления productIds
        state.productIds = action.payload;
      },
    },
});

export const { setGeneration, setBuklet, setAccessType, setquantity, setCity, setProductIds } = postSlice.actions; // Добавлено setProductIds в экспорт

export default postSlice.reducer;
