import React, { useState, useEffect } from 'react';
import Header from './Header';
import TabsComponent from './Tabs';
import NotificationPrompt from '../Notification/NotificationPromt';
import { hasSubscription } from '../Notification/script.js';

export default function New(props) {
    const [isSubscribed, setIsSubscribed] = useState(false);

    useEffect(() => {
        const checkSubscription = async () => {
            const result = await hasSubscription();
            setIsSubscribed(result);
        };

        checkSubscription();
    }, []);

    return (
        <div>
            <Header />
            <TabsComponent />
            
            {/* Условный рендеринг компонента NotificationPrompt */}
            {!isSubscribed && <NotificationPrompt/>}
            {props.children}
        </div>
    );
}
