import React, { useState } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { useParams } from "react-router-dom";

// Импорт функции main
import { main } from './script.js'; // Убедитесь, что путь к файлу script.js указан правильно

function NotificationPrompt() {
  const [open, setOpen] = useState(true);
  const { accountId } = useParams();
  const handleClose = () => {
    setOpen(false);
  };

  const allowedPush = async () => {
    // Передаем accountId в функцию main
    await main(accountId);
    handleClose();
};


  return (
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle>{"Уведомления"}</DialogTitle>
      <DialogContent>
        <p>Вы хотели бы получать уведомления?</p>
      </DialogContent>
      <DialogActions sx={{justifyContent: "center"}}>
        <Button onClick={handleClose} variant="text" sx={{mr:'50px'}}>Отменить</Button>
        <Button onClick={allowedPush} variant="contained"  sx={{ml:'50px'}}>Включить</Button>
      </DialogActions>
    </Dialog>
  );
}

export default NotificationPrompt;
